@mixin list-reset {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin hide-scroll {
  scrollbar-width: none;
  &::-webkit-scrollbar {
    display: none;
  }
}

@mixin transition($args...) {
  transition-timing-function: ease-out;
  transition-duration: 0.2s;
  transition-property: $args;
}

@mixin word-break {
  word-break: break-word;
  overflow-wrap: break-word;
}

@mixin text-truncate {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

@mixin text-truncate-multiline($row-count: 3) {
  -webkit-box-orient: vertical;
  -webkit-line-clamp: $row-count;
  display: -webkit-box;
  overflow: hidden;
}

// @mixin ultra-wide {
//   @media (max-height: $ultra-wide-height) {
//     @content;
//   }
// }
@mixin ultra-wide {
  @media (aspect-ratio > 16/9) {
    @content;
  }
}

@mixin mobile {
  @media (max-width: $mobile-width) {
    @content;
  }
}

@mixin mobile-px {
  @media (max-width: $mobile-px-width) {
    @content;
  }
}
